.mask-qr-layout {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    p {
      padding-top: 100px;
    }
    img {
      width: 80%;
      height: 80%;
    }
    h2 {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
  
      color: rgba(5, 21, 21, 0.62);
      font-size: 24px;
      font-weight: 400;
      line-height: 1.33;
      margin: 10px 0 20px;
    }
    h3 {
      margin-left: 10px;
      color: #a9d18e;
      font-weight: 300;
    }
    .b{
        color: #61943E;
        font-weight: 700;
    }
  }
  